import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  signals= [];

  constructor(
    public navCtrl: NavController,
    private db: AngularFireDatabase ) {
      db.list('/signals').subscribe(data=>{
        this.signals = [];
        var x = 0;
        for (let signal of data){
          if(signal.E_Color == "red"){
            signal.Trend = "Sell"
            signal.E_Trend = "arrow-round-down"
          } else {
            signal.Trend = "Buy"
            signal.E_Trend = "arrow-round-up"
          }
          if(x == 0){
            signal.background = "#9F99B5";
            x = 1;
          } else{
            signal.background = "#D7DBDD";
            x = 0;
          }
          this.signals.unshift(signal);
          //this.signals.unshift()
        }
      });
      
  }

}



